.acf-block-preview .b-image-text img {
  display: block;
  width: 100%;
  height: auto;
}
.acf-block-preview .b-quote p {
  display: inline-block;
  position: relative;
  margin-bottom: 0;
  font-style: italic;
  font-size: 20px;
}
.acf-block-preview .b-quote p::before, .acf-block-preview .b-quote p::after {
  content: "";
  background: url("../img/default/icon-quote.svg") no-repeat;
  background-size: 15px;
  width: 15px;
  height: 15px;
  position: absolute;
}
@media (max-width: 575.98px) {
  .acf-block-preview .b-quote p::before, .acf-block-preview .b-quote p::after {
    background-size: 10px;
    width: 10px;
    height: 10px;
  }
}
.acf-block-preview .b-quote p::before {
  transform: scale(-1, 1);
  left: -25px;
  top: 0;
}
.acf-block-preview .b-quote p::after {
  right: -25px;
  bottom: -5px;
}
.acf-block-preview .b-slider img {
  display: block;
  width: 100%;
  height: auto;
}
.acf-block-preview .b-video__play {
  display: block;
  position: relative;
  overflow: hidden;
}
.acf-block-preview .b-video__play::after {
  margin: 0 auto;
  background: url("../img/default/icon-play.svg") no-repeat;
  background-size: 100%;
  width: 100px;
  height: 100px;
  content: "";
  position: absolute;
  top: calc(50% - 25px);
  transform: translateY(-50%);
  left: 0;
  right: 0;
  opacity: 0.8;
  transition: 0.3s all ease-in;
}
@media (max-width: 767.98px) {
  .acf-block-preview .b-video__play::after {
    width: 50px;
    height: 50px;
  }
}
.acf-block-preview .b-video__play:hover::after {
  opacity: 1;
}
.acf-block-preview .b-video__play img {
  display: block;
  width: 100%;
  height: auto;
}

